import './page-footer.scss';
import React from 'react';
import { Grid } from '@material-ui/core';
import { useLocation, useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

const PageFooter = () => {
  const [t, i18n] = useTranslation();
  const navigate = useNavigate();
  const currLocation = useLocation();
  const navigateTo = (dest) => {
    if (dest !== currLocation.pathname) {
      navigate(dest);
    } else {
      window.location.reload();
    }
  };
  return (
    <div>
      <footer className="site-footer" role="contentinfo">
        <div className="page-width">
          <div className="site-footer__content">
            <div className="site-footer__item site-footer__item--center site-footer__item--full-width">
              <div className="site-footer__item-inner site-footer__item-inner--link_list">
                <ul className="site-footer__linklist list--inline">
                  <li className="site-footer__linklist-item">
                    <a href="/pages/contact-us">
                      Contact Us
                    </a>
                  </li>
                  <li className="site-footer__linklist-item">
                    <a href="/pages/faq">
                      FAQ
                    </a>
                  </li>
                  <li className="site-footer__linklist-item">
                    <a href="/policies/terms-of-service">
                      Terms of Service
                    </a>
                  </li>
                  <li className="site-footer__linklist-item">
                    <a href="/policies/refund-policy">
                      Refund policy
                    </a>
                  </li>
                  <li className="site-footer__linklist-item">
                    <a href="/pages/locate-us">
                      Locate Us
                    </a>
                  </li>
                  <li className="site-footer__linklist-item">
                    <a href="/blogs/bubuchichis-blog">
                      Blog
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <small className="site-footer__copyright-content">
          from&nbsp;<a href="/">bubuchichis</a>, with 
        </small>
      </footer>
    </div>
  );
};

export default PageFooter;
