const currencyDict = {
  sgd: {
    label: 'SGD',
    exchangeRate: 1,
  },
  usd: {
    label: 'USD',
    exchangeRate: 0.76,
  },
};

const getIdFromProductId = (productId) => {
  const paramArr = productId.split('-');
  const itemCodeArr = paramArr[paramArr.length - 1].split('.');
  return itemCodeArr[1];
};

const RadioButton = ({ disabled, ...props }) => {
  return (
    <Radio
      className="radio-button"
      disableRipple
      aria-disabled={disabled}
      color="default"
      checkedIcon={<div />}
      icon={<div />}
      {...props}
    />
  );
};

RadioButton.propTypes = {
  disabled: PropTypes.bool.isRequired,
};

const VariantRadioField = ({ currVariantIndex, variants, handleVariantChange }) => {
  // eslint-disable-next-line
  console.log(variants);
  return (
    <FormControl>
      <FormLabel className="options-title">
        <p>Select a variant:</p>
      </FormLabel>
      <RadioGroup
        className="options-container"
        value={currVariantIndex}
        aria-labelledby="variant-radios"
        name="variant-radios"
        onChange={handleVariantChange}
      >
        {variants.map((variant, i) => {
          let labelClassSuffix = '__default';
          if (variant.isSoldOut) {
            labelClassSuffix = '__disabled';
          } else if (i === currVariantIndex) {
            labelClassSuffix = '__selected';
          }
          return (
            <FormControlLabel
              className="no-radio-option"
              key={variant.id}
              value={!variant.isSoldOut ? i : 'disabled'}
              control={<RadioButton disabled={variant.isSoldOut} />}
              label={
                <p className={`option-label option-label${labelClassSuffix}`}>
                  {variant.name}
                </p>
              }
            />
          );
        })}
      </RadioGroup>
    </FormControl>
  );
};

VariantRadioField.propTypes = {
  currVariantIndex: PropTypes.number.isRequired,
  variants: PropTypes.array.isRequired,
  handleVariantChange: PropTypes.func.isRequired,
};

const ProductContainer = () => {
  // temp const
  const currCurrency = 'sgd';

  const { productId } = useParams();
  const id = getIdFromProductId(productId);
  const product = mockProducts[id];
  // eslint-disable-next-line
  console.log(product);
  const [currVariantIndex, setCurrVariantIndex] = useState(-1);
  const getPriceRangeString = (currency, type, variants) => {
    if (!(currency in currencyDict)) {
      return 'invalidCurrency error';
    }
    let maxPrice = -1;
    let minPrice = 99999;
    for (let i = 0; i < variants.length; i += 1) {
      const currPrice = variants[i].price[type] * currencyDict[currency].exchangeRate;
      maxPrice = maxPrice < currPrice ? currPrice : maxPrice;
      minPrice = minPrice > currPrice ? currPrice : minPrice;
    }
    if (maxPrice < 0) {
      return 'Not Available';
    }
    const currencyString = currencyDict[currency].label;
    return `${currencyString}${minPrice.toFixed(2)} - ${currencyString}${maxPrice.toFixed(2)}`;
  };
  const fullPriceRangeString = product?.variants ? getPriceRangeString(currCurrency, 'full', product.variants) : '';
  const discPriceRangeString = product?.variants ? getPriceRangeString(currCurrency, 'discounted', product.variants) : '';
  const [currFullPriceString, setCurrFullPriceString] = useState(fullPriceRangeString);
  const handleVariantChange = e => {
    // eslint-disable-next-line
    console.log(e);
    e.stopPropagation();
    let value = currVariantIndex;
    let fullPriceString = currFullPriceString;
    if (e.target.value !== 'disabled') {
      value = parseInt(e.target.value, 10);
      const fullPriceMagnitude = (product.variants[value].price.full * currencyDict[currCurrency].exchangeRate).toFixed(2);
      fullPriceString = `${currencyDict[currCurrency].label}${fullPriceMagnitude}`;
    }
    setCurrVariantIndex(value);
    setCurrFullPriceString(fullPriceString);
  };
  // eslint-disable-next-line
  console.log(product.variants);
  return (
    <div className="page-width">
      <Grid container className="product-container" spacing={2}>
        <Grid item sm={6} xs={12}>
          <img className="product-image" src={product.imageUrlList[0]} alt={product.imageUrl} />
        </Grid>
        <Grid item sm={6} xs={12}>
          <Grid container className="product-info" style={{ marginLeft: '30px', top: '110px' }}>
            <Grid item xs={12}>
              <h1 className="page-title product-title">{product.name}</h1>
            </Grid>
            <Grid item xs={12}>
              <div className="price--large">
                <span>{currFullPriceString}</span>
              </div>
            </Grid>
            <Grid item xs={12}>
              <VariantRadioField currVariantIndex={currVariantIndex} variants={product.variants} handleVariantChange={handleVariantChange} />
            </Grid>
            <Grid item xs={12}>
              <div>
                <h2>Description</h2>
                <ReactMarkdown remarkPlugins={[remarkGfm]}>{product.description}</ReactMarkdown>
              </div>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
};

export default ProductContainer;
