import './announcement-bar.scss';
import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

const AnnouncementBar = () => {
  const [t, i18n] = useTranslation();
  const navigate = useNavigate();
  const currLocation = useLocation();
  const navigateTo = (dest) => {
    if (dest !== currLocation.pathname) {
      navigate(dest);
    } else {
      window.location.reload();
    }
  };
  return (
    <div className="announcement-bar" role="region" aria-label="Announcement">
      <p className="announcement-bar__message">
        FREE DELIVERY FOR ORDERS ABOVE $120 | $4 DELIVERY FOR ORDERS ABOVE $38 | $8 DELIVERY FOR ORDERS BELOW $38
      </p>
    </div>
  );
};

export default AnnouncementBar;
