import './product-list-container.scss';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import axios from 'axios';
import {
  Grid, Paper,
} from '@material-ui/core';
import { useNavigate } from 'react-router-dom';

const ProductCard = ({ product }) => {
  const [isHover, setIsHover] = useState(false);
  const navigate = useNavigate();
  const redirectToProductPage = () => {
    const path = `/product/${product.name.replace(/\s+/g, '-')}-i.${product.id}`;
    navigate(path);
  };
  return (
    <div
      className={`product-card${isHover ? ' hover' : ''}`}
      onMouseEnter={() => setIsHover(true)}
      onMouseLeave={() => setIsHover(false)}
      onClick={() => redirectToProductPage()}
      onKeyDown={() => redirectToProductPage()}
      tabIndex="0"
      role="menuitem"
    >
      <div className="product-card__image-container">
        <img className={`product-card__image${isHover ? ' hover' : ''}`} src={product.imageUrl} alt={product.imageUrl} />
      </div>
      <div className={`product-card__name${isHover ? ' hover' : ''}`}>{product.name}</div>
      <div className={`product-card__price${isHover ? ' hover' : ''}`}>{product.price}</div>
    </div>
  );
};

ProductCard.propTypes = {
  product: PropTypes.object.isRequired,
};

/** contains product grids */
const ProductGrids = ({ products }) => {
  let productsProcessed = [];
  for (let i = 0; i < products.length; i += 3) {
    productsProcessed = productsProcessed.concat([products.slice(i, i + 3)]);
  }
  return (
    <div className="product-list-container">
      <Grid container spacing={2}>
        {productsProcessed.map((row, i) => (
          <Grid item container className="product-row" xs={12} spacing={2}>
            {row.map((item, j) => (
              <Grid item xs={4}>
                <ProductCard product={item} />
              </Grid>
            ))}
          </Grid>
        ))}
      </Grid>
    </div>
  );
};

ProductGrids.propTypes = {
  products: PropTypes.array.isRequired,
};

